/*eslint-disable */
import { getSymbolsInSelection, getNextGroupedSymbols } from '../';

const pushCursorToStack = (valueStack, key, selectionStart, pattern, pastedLength = null) => {
  const symbolsInSelection = getSymbolsInSelection(0, selectionStart, pattern);

  switch (key) {
    case 'Delete':
    case 'Cut':
      valueStack.splice(selectionStart - symbolsInSelection, 0, '|');
      break;

    case 'Backspace':
      // We need to consider that a char has been deleted with selectionStart - 1
      // Also we don't need to delete anything here as onChange value has been already deleted

      const nextGroupedSymbols = getNextGroupedSymbols(selectionStart, pattern, 'reverse');
      if (nextGroupedSymbols) {
        valueStack.splice(selectionStart - symbolsInSelection - 1, 1, '|');
      } else {
        const cursor = selectionStart - symbolsInSelection - 1;
        valueStack.splice(cursor, 0, '|');
      }
      break;

    case 'Paste':
      valueStack.splice(selectionStart - symbolsInSelection + pastedLength, 0, '|');
      break;

    default:
      valueStack.splice(selectionStart - symbolsInSelection + 1, 0, '|');
      break;
  }

  return valueStack;
};

export default pushCursorToStack;



