/*eslint-disable*/
import React, { PureComponent } from 'react';
import Types from 'prop-types';
import {
  formatWithPattern,
  unformatWithPattern,
  pushCursorToStack,
  HistoryNavigator,
} from '../common/textFormat';

class InputText extends PureComponent {
  static propTypes = {
    pattern: Types.string,
    placeholder: Types.string,
  };

  static defaultProps = {
    pattern: '',
    placeholder: '',
  };

  constructor(props) {
    super(props);
    this.state = {
      value: '',
      valueStack: [],
      isCutting: false,
      isPasting: false,
      isRedo: false,
      isUndo: false,
      pastedLength: 0,
      historyNavigator: new HistoryNavigator(),
    };
    this.input = React.createRef();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (prevState.prevPattern !== nextProps.pattern) {
      const { value, prevPattern } = prevState;
      const { pattern } = nextProps;
      const unFormattedValue = unformatWithPattern(value, prevPattern);
      const formattedValue = formatWithPattern(unFormattedValue, pattern);
      return {
        prevPattern: pattern,
        value: formattedValue,
      };
    }
    return null;
  }

  setCursorPosition = select => {
    setTimeout(() => {
      this.input.current.selectionStart = select;
      this.input.current.selectionEnd = select;
    }, 0);
  };

  handleOnKeyDown = e => {
    const { selectionStart } = this.input.current;
    let isRedo = false;
    let isUndo = false;
    let charCode = String.fromCharCode(e.which).toLowerCase();

    if ((e.ctrlKey || e.metaKey) && charCode === 'z') {
      if (e.shiftKey) {
        isRedo = true;
      } else {
        isUndo = true;
      }
    }

    this.setState({ lastKey: e.key, selectionStart, isUndo, isRedo });
  };

  onPaste = e => {
    let pastedChar = e.clipboardData.getData('Text').split('');
    const { pattern } = this.props;
    pastedChar = pastedChar.filter(el => pattern.split('').indexOf(el) === -1);

    this.setState({ isPasting: true, pastedLength: pastedChar.length });
  };

  handleOnChange = e => {
    const { value } = e.target;
    const { pattern } = this.props;
    const {
      lastKey,
      selectionStart,
      isCutting,
      isPasting,
      isUndo,
      isRedo,
      pastedLength,
      historyNavigator,
    } = this.state;

    let newKey = lastKey;
    let newValue = '';

    if (isUndo) {
      newValue = historyNavigator.getHistory('undo');
    } else if (isRedo) {
      newValue = historyNavigator.getHistory('redo');
    } else {
      if (isPasting) {
        newKey = 'Paste';
      }
      if (isCutting) {
        newKey = 'Cut';
      }

      const valueStackUnformatted = unformatWithPattern(value, pattern).split('');

      // Handles cursor.
      pushCursorToStack(valueStackUnformatted, newKey, selectionStart, pattern, pastedLength);

      const valueStackFormatted = formatWithPattern(valueStackUnformatted.join(''), pattern).split(
        '',
      );

      this.setCursorPosition(valueStackFormatted.indexOf('|'));

      const newStack = valueStackFormatted.filter(char => char !== '|');
      newValue = formatWithPattern(unformatWithPattern(newStack.join(''), pattern), pattern);
      historyNavigator.addHistory(newValue);
    }

    this.setState({ value: newValue }, this.resetEvents());
  };

  getFormattedValue = newStackWithCursor => {
    const { pattern } = this.props;
    const newStack = newStackWithCursor.filter(char => char !== '|');
    return formatWithPattern(newStack.join(''), pattern);
  };

  resetEvents = () => {
    this.setState({
      isCutting: false,
      isPasting: false,
      isUndo: false,
      isRedo: false,
      pastedLength: 0,
    });
  };

  render() {
    const { value } = this.state;
    const { placeholder } = this.props;

    return (
      <input
        type="text"
        value={value}
        ref={this.input}
        className="form-control"
        onKeyDown={e => this.handleOnKeyDown(e)}
        onChange={e => this.handleOnChange(e)}
        placeholder={placeholder}
        onCut={() => this.setState({ isCutting: true })}
        onPaste={e => this.onPaste(e)}
      />
    );
  }
}

export default InputText;



